//VueX 文章列表模块
import axios from '../../plugins/axios'
export default {
    state: () => ({
        list: []
    }),
    mutations: {
        getPost(state, payload) {
            state.list = payload
            // console.log(state.list)
        },
        loadMore(state, list) {
            console.log('发送成功')
            state.list = [...state.list, ...list]

        }
    },
    actions: {
        //获取文章数据
        //请求文章列表有两种情况，第一个初始请求，第二个加载更多请求，依据传参带不带page属性，不带事初始，带了是加载更多
        async getPost({ commit }, params) {
            //page:页数 tab:分类 limit:每一页数量 
            const { page, tab } = params
            const search = page ? `?tab=${tab}&page=${page}&limit=20` : `?tab=${tab}&limit=40`
            const list = await axios.get(`/topics${search}`)

            page ? commit('loadMore', list.data) : commit('getPost', list.data)
        },

    },
    modules: {

    }


}