//文章详情模块
//VueX 文章列表模块
import axios from '../../plugins/axios'
export default {
    state: () => ({
        //对象的初始值一般写 null 不写成 {}
        post: null
    }),


    mutations: {
        getId(state, payload) {
            state.post = payload
        }
    },

    actions: {
        async getId({ commit }, id) {
            const value = await axios.get(`/topic/${id}`)
            // console.log(value)
            commit('getId', value.data)
        }
    },
}